@import url(https://fonts.googleapis.com/css2?family=Work+Sans:wght@200;400&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html {
  box-sizing: border-box;
}
*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  height: 100vh;
  display: flex;
  justify-content: center;
  background-image: linear-gradient(80deg, #52fade 0%, #6e52fa 100%);
  font-family: "Work Sans", sans-serif;
}

label {
  padding: 0.4rem;
}

.addTaskInput {
  margin: 0.4rem;
  font-family: "Work Sans", sans-serif;

  background-color: transparent;
  color: white;
  border-bottom: 1px solid white;
}

input:focus {
  outline: none;
}

.openAddTaskButton {
  margin-bottom: 13px;
}

.openAddTaskButton:hover,
.addTaskButton:hover {
  color: white;
  border: 1px solid white;
}

.addTaskButton {
  padding: 0.25rem 0.5rem 0.25rem 0.5rem;
  margin-bottom: 18px;
}

.display {
  padding: 0 0.7rem 0 0.7rem;
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.3rem;
}

.doneButton {
  margin-right: 0.3rem;
}

.deleteButton {
  margin-left: 0.3rem;
}

input {
  border: none;
  margin: 0 0.4rem 0 3.43rem;
  font-family: "Work Sans", sans-serif;
  font-size: 1rem;
  background-color: transparent;
  color: black;
  border-bottom: 1px solid black;
}

.display:hover {
  background-color: rgba(255, 255, 255, 0.4);
}

.Main {
  background-color: rgb(242, 242, 242, 0.4);
  width: 600px;
  height: 80vh;
  margin-top: 2rem;
  text-align: center;
  box-shadow: 10px 10px 12px rgba(58, 58, 58, 0.4);
}

.header {
  background-color: #2a527a;
  color: white;
  height: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

h1 {
  font-size: 3rem;
  font-weight: 100;
  position: relative;
}

h1::after {
  content: "";
  height: 1px;
  width: 330px;
  background-color: rgb(255, 255, 255, 1);
  position: absolute;
  left: -5px;
  top: 62px;
}

button {
  background-color: #e6fa3c;
  border: 1px solid #e6fa3c;
  font-size: 0.8rem;
  padding: 0.6rem;
  border-radius: 0;
  font-family: "Work Sans", sans-serif;
  cursor: pointer;
}

button:hover {
  background-color: transparent;
  color: black;
  border: 1px solid black;
}

.category {
  text-align: left;
  padding-left: 1rem;
  font-weight: 400;
  position: relative;
}

.category::after {
  content: "";
  height: 2px;
  width: 120px;
  background-color: rgba(0, 0, 0);
  position: absolute;
  left: 12px;
  top: 21px;
}

